<template>
  <div>
    <div class="title">
        设备台账
    </div>
    <el-alert
        title="台账信息的初始化工作请到系统设置-设备台账初始化中进行设置 注：修改即生效"
        type="warning"
        :closable="false">
    </el-alert>

    <div class="tabBox">
        <el-table
            :data="tableData"
            :fit="true"
            style="width: 100%">
            <el-table-column
              prop="date"
              label="字段名"
              width="180">
            </el-table-column>
            <el-table-column
              prop="name"
              label="值"
              width="180">
                <template slot-scope="scope">
                    <!-- Form -->
                    <!-- <el-button type="text" @click="dialogFormVisible = true">打开嵌套表单的 Dialog</el-button> -->
                    <div class="valCSS" @click="dialogFormVisible = true">&nbsp;{{form.name=='' ? '点击输入值':form.name}}</div>
                    <el-dialog title="填写值" :visible.sync="dialogFormVisible">
                    <el-form :model="form">
                        <el-form-item label="" :label-width="formLabelWidth">
                        <el-input v-model="form.name" autocomplete="off"></el-input>
                        </el-form-item>
                    </el-form>
                    <div slot="footer" class="dialog-footer">
                        <el-button @click="dialogFormVisible = false">取 消</el-button>
                        <el-button type="primary" @click="dialogFormVisible = false">确 定</el-button>
                    </div>
                    </el-dialog>
                </template>
            </el-table-column>
            <el-table-column
              prop="address"
              label="单位">
            </el-table-column>
        </el-table>
    </div>
  </div>
</template>

<script>
export default {
    data(){
        return {
            tableData: [{
            date: '2016-05-02',
            name: '王小虎',
            address: '上海市普陀区金沙江路 1518 弄',
            }],
            dialogTableVisible: false,
            dialogFormVisible: false,
            form: {
            name: ''
            },
            formLabelWidth: '120px'
            }
    },
    methods:{
        Fnval(){//这是点击值的时候
            this.dialogFormVisible=true
            console.log('点击则个显示值')
        }
    }
}
</script>

<style lang="stylus" scoped>
.title
    font-size 20px
    color #333
    margin-bottom 20px
    margin-top 20px
    padding-left 15px
    border-left 3px solid #409EFF
.tabBox
    padding 45px
    .valCSS
        width 100%
        height 100%
        font-size 14px
        color #666
</style>


